.contact-container {
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
    padding: 20px;
    gap: 20px; /* Space between items */
    box-sizing: border-box; /* Include padding and border in the element's total width and height */
}

.map {
    flex: 1 1 100%; /* Full width on small screens */
    max-width: 100%; /* Prevent overflow */
    margin: 0 auto; /* Center map */
}

.contact-info {
    flex: 1 1 100%; /* Full width on small screens */
    max-width: 100%; /* Prevent overflow */
    text-align: justify; /* Justify text alignment */
    margin: 0 0; /* Center contact info */
}

@media (min-width: 801px) {
    .contact-container {
        flex-direction: row; /* Side by side on larger screens */
    }

    .map {
        flex: 1 1 400px; /* Map width */
        margin-right: 20px; /* Space between map and contact info */
    }

    .contact-info {
        flex: 1 1 400px; /* Contact info width */
        max-width: 400px; /* Restrict to the map width */
    }
}

@media (max-width: 800px) {
    .contact-info {
        text-align: center; /* Center text on small screens */
    }
}